<template>
  <div class="operate-task-log">
    <div class="topTitle">
      <span class="topTitle-span1">过程管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">过程内容</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">机构洗钱风险管理</span>
      <div class="button-content">
        <Button
          type="primary"
          @click="addfun('addFlag')"
          v-authcontrol="{menuCode:'108001002015001'}"
          v-if="firstLoginFlag"
        >
          <i class="ivu-icon ivu-icon-md-add"></i>新增
        </Button>
        <Button
          type="primary"
          v-authcontrol="{menuCode:'108001002015004'}"
          v-if="firstLoginFlag"
          @click="exportFn"
        >
          <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button>
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div class="contentCellBody1">
          <Form ref="formValidate" :model="formValidate" :label-width="120" inline>
            <Row>
              <FormItem label="标题：" prop>
                <Input v-model="formValidate.val1" placeholder="请输入标题" style="width:200px"></Input>
              </FormItem>
              <FormItem label="发布时间：">
                <DatePicker
                  @on-change="datechange"
                  :value="formValidate.val2"
                  format="yyyy-MM-dd"
                  type="daterange"
                  placement="bottom-end"
                  placeholder="请选择时间"
                  style="width: 200px"
                ></DatePicker>
              </FormItem>
              <FormItem label="状态：" prop="extraUnit">
                <Select v-model="formValidate.val3" filterable style="width:200px">
                  <Option value>请选择</Option>
                  <Option
                    :value="item.code"
                    v-for="(item,index) in PROCESS_CHECK_STATE"
                    :key="index"
                  >{{item.name}}</Option>
                </Select>
              </FormItem>
            </Row>
            <Row>
              <div class="btnClass">
                <Button type="primary" @click="handleSubmit('formValidate','0')">查询</Button>
                <Button type="primary" @click="handleSubmit('formValidate','1')">精确查询</Button>
              </div>
            </Row>
          </Form>
        </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in tableBody">
                  <td>{{pageSize*(pageNum-1)+index+1}}</td>
                  <td style="width:100px">
                    <span
                      class="spanBox"
                      v-authcontrol="{menuCode:'108001002015002'}"
                      v-if="firstLoginFlag"
                      @click="addfun('modifyFlag',item.busId,item.id,item.extraCode)"
                    >修改</span>
                    <span
                      class="spanBox"
                      @click="delItem(item.id,item.extraCode)"
                      v-authcontrol="{menuCode:'108001002015003'}"
                      v-if="firstLoginFlag"
                    >删除</span>
                    <span
                      class="spanBox"
                      v-authcontrol="{menuCode:'108001002015007'}"
                      v-if="firstLoginFlag"
                      @click="addfun('appendFlag',item.busId,item.id,item.flag)"
                    >追加</span>
                  </td>

                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.title"
                    >
                      <span
                        slot="reference"
                        class="spanBox textMaxWidth disBlock textOverflow"
                        @click="watchTableCellFun(item,item.busId,item.id)"
                      >{{item.title}}</span>
                    </el-popover>
                  </td>
                  <td class="spanBox" @click="showProcess(item)">{{item.flag}}</td>
                  <td>{{item.ifShow=='true'?'是':'否'}}</td>
                  <td>
                    <el-popover
                      placement="top-start"
                      title
                      width="200"
                      trigger="hover"
                      :content="item.manager"
                    >
                      <p
                        slot="reference"
                        class="textOverflow disBlock textMaxWidth"
                      >{{item.manager}}</p>
                    </el-popover>
                  </td>
                  <td class="spanBox" @click="watchAppendFun(item,item.busId,item.id)">查看</td>
                  <td class="spanBox" @click="watchFileFun(item.busId,item.id,item)">查看</td>
                  <td>{{item.pubUser}}</td>
                  <td>{{item.pubTime}}</td>
                  <td>{{item.updater}}</td>
                  <td>{{item.updateTime}}</td>
                </tr>
              </tbody>
            </table>
          </Row>
        </div>
        <div class="flexCenter pageBox">
          <Page
            :total="total"
            :current.sync="pageNum"
            show-elevator
            show-sizer
            @on-change="pageChangeFun"
            @on-page-size-change="pageSizeChange"
          ></Page>
        </div>
      </div>
    </div>
    <!-- ===========弹框 -->
    <!-- <Button @click="modal1 = true">Custom header and footer</Button> -->
    <Modal :mask-closable="false" v-model="modal1" width="1000" class="dialog" title="设置">
      <div style="height:400px;overflow-y: auto;" class="modelSonDiv">
        <Form
          ref="formValidateModel1"
          :model="formValidateModel1"
          :rules="ruleValidate"
          :label-width="120"
        >
          <FormItem label="流程标题" prop="bpmTitle">
            <Input v-model="formValidateModel1.bpmTitle" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="标题" prop="val1">
            <Input v-model="formValidateModel1.val1" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="是否公开" prop>
            <i-switch v-model="formValidateModel1.val2" size="large" :disabled="this.addFlag">
              <span slot="true">On</span>
              <span slot="false">Off</span>
            </i-switch>
          </FormItem>
          <FormItem label="编辑内容" prop="val3">
            <Input
              v-model="formValidateModel1.val3"
              type="textarea"
              :autosize="{minRows: 2,maxRows: 5}"
              placeholder="请输入"
              :disabled="this.addFlag"
            ></Input>
          </FormItem>
          <FormItem label="上传附件">
            <showUsualFiles
              :file-obj="dataObj"
              :key="fileId"
              @requestUploadList="receiveUploadList"
            ></showUsualFiles>
          </FormItem>
          <Row v-if="addFlagShow">
            <Row style="text-again:left;border-bottom:1px dashed #E8EAEC;margin-bottom:20px">追加信息</Row>
            <FormItem label="上传附件">
              <showUsualFiles
                :file-obj="dataObj2"
                :key="fileId2"
                @requestUploadList="receiveUploadList2"
              ></showUsualFiles>
            </FormItem>
            <FormItem label="追加内容">
              <Input
                v-model="formValidateModel1.val11"
                type="textarea"
                :autosize="{minRows: 2,maxRows: 5}"
                placeholder="请输入"
              ></Input>
            </FormItem>
          </Row>
          <Modal :mask-closable="false" v-model="taskPeipleModel" width="360" class="treeBoxModel">
            <p slot="header" style="color:#f60;text-align:center">
              <span>审核人信息</span>
            </p>
            <div style="text-align:left;overflow-y:auto;height:300px">
              <FormItem prop="nextUser" :label-width="10">
                <Tree
                  ref="treeRef"
                  :data="data2"
                  show-checkbox
                  multiple
                  @on-check-change="updateNextUser"
                ></Tree>
              </FormItem>
            </div>
            <div slot="footer">
              <Button
                type="primary"
                size="large"
                long
                @click="handleSubmitLast('formValidateModel1')"
                :disabled="buttonDisabled"
              >立即申请</Button>
            </div>
          </Modal>
        </Form>
      </div>
      <div slot="footer" class="flexCenter">
        <div class="button-content">
          <Button type="primary" @click="save1('formValidateModel1')">{{buttonVal}}</Button>
          <Button @click="modal1=false">取消</Button>
        </div>
      </div>
    </Modal>
    <!-- ============== -->
    <Modal :mask-closable="false" v-model="watchFileModel" width="90%" title="查看">
      <showUsualFiles :file-obj="dataObj3" :key="fileId3" @requestUploadList="receiveUploadList3"></showUsualFiles>
    </Modal>
    <!-- ===========弹框 -->
    <!-- ============== -->
    <Modal :mask-closable="false" v-model="watchTableCellDitalModel" width="90%" title="查看">
      <div class="content">
        <Form :label-width="120">
          <FormItem label="流程标题：">
            <label>{{watchTableCell.bpmTitle}}</label>
          </FormItem>
          <FormItem label="标题：">
            <label>{{watchTableCell.val1}}</label>
          </FormItem>
          <FormItem label="是否公开：">
            <label>{{watchTableCell.val2=='true'?'是':'否'}}</label>
          </FormItem>
          <FormItem label="编辑内容：">
            <label>
              <!-- {{watchTableCell.val3}} -->
              <el-popover
                placement="top-start"
                title
                width="200"
                trigger="hover"
                :content="watchTableCell.val3"
              >
                <p slot="reference" class="textMaxWidth2 textOverflow">{{watchTableCell.val3}}</p>
              </el-popover>
            </label>
          </FormItem>
          <FormItem label="附件：">
            <showUsualFiles
              :file-obj="dataObj4"
              :key="fileId4"
              @requestUploadList="receiveUploadList4"
            ></showUsualFiles>
          </FormItem>
          <FormItem label="追加附件：">
            <showUsualFiles
              :file-obj="dataObj5"
              :key="fileId5"
              @requestUploadList="receiveUploadList5"
            ></showUsualFiles>
          </FormItem>
          <FormItem label="追加内容：">
            <label>
              <!-- {{watchTableCell.val11}} -->
              <el-popover
                placement="top-start"
                title
                width="200"
                trigger="hover"
                :content="watchTableCell.val11"
              >
                <p slot="reference" class="textMaxWidth2 textOverflow">{{watchTableCell.val11}}</p>
              </el-popover>
            </label>
          </FormItem>
        </Form>
      </div>
    </Modal>
    <!-- ===========弹框 -->
    <!-- ============== -->
    <Modal :mask-closable="false" v-model="watchAppenModel" width="99%" title="查看">
      <div class="content">
        <Row>
          <table class="tableBox">
            <thead>
              <tr>
                <td v-for="(item,index) in tableHeaderAppend">{{item.name}}</td>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item,index) in tableBodyAppend">
                <td>{{index+1}}</td>
                <!-- <el-popover
                  placement="top-start"
                  title
                  width="200"
                  trigger="hover"
                  :content="item.content"
                >
                  <td slot="reference" class="textOverflow textMaxWidth textTdTop">{{item.content}}</td>
                </el-popover>-->
                <td>
                  <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="item.content"
                  >
                    <p slot="reference" class="textOverflow textMaxWidth">{{item.content}}</p>
                  </el-popover>
                </td>
                <td class="spanBox" @click="watchAppenModelAppendFile(item)">查看</td>
                <td>{{item.dept}}</td>
                <td>{{item.createTime}}</td>
                <td>{{item.creator}}</td>
              </tr>
            </tbody>
          </table>
        </Row>
      </div>
    </Modal>
    <!-- ===========弹框 -->
    <!-- ============== -->
    <Modal :mask-closable="false" v-model="watchAppenModelAppendFileModel" width="80%" title="查看">
      <showUsualFiles :file-obj="dataObj6" :key="fileId6" @requestUploadList="receiveUploadList6"></showUsualFiles>
    </Modal>
    <!-- ===========弹框 -->
    <Modal :mask-closable="false" v-model="taskPeipleModel2" width="360" class="treeBoxModel">
      <p slot="header" style="color:#f60;text-align:center">
        <span>审核人信息</span>
      </p>
      <div style="text-align:left;overflow-y:auto;height:300px">
        <Form ref="taskPeipleModelData2" :model="taskPeipleModelData2" :rules="ruleValidate2">
          <FormItem prop="nextUser" :label-width="10">
            <Tree
              ref="treeRef2"
              :data="data2"
              show-checkbox
              multiple
              @on-check-change="updateNextUser2"
            ></Tree>
          </FormItem>
        </Form>
      </div>
      <div slot="footer">
        <Button
          type="primary"
          size="large"
          long
          @click="handleSubmitLast2('taskPeipleModelData2')"
        >立即申请</Button>
      </div>
    </Modal>
    <Modal :mask-closable="false" v-model="showModal" title="流程详情" class="dialog" width="80%">
      <processMessage :data-obj="dataObj" v-if="showModal"></processMessage>
      <div slot="footer"></div>
    </Modal>
    <!-- ========弹框 -->
  </div>
</template>
<script>
import processMessage from "@/components/moneyRiskAssess/common/processMessage"; //过程信息
import showUsualFiles from "@/components/commonComp/showUsualFiles";
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  components: { Treeselect, showUsualFiles, processMessage },
  created() {
    this.tableList();
    let that = this;
    this.axios
      .post("/system/unit/getExtractUnit")
      .then(response => {
        if (response.code == "200") {
          that.selectTreeCleanUp(response.data);
          that.data1 = response.data;
        }
      })
      .catch(error => {});
    this.axios
      .get("/system/getDic/PROCESS_CHECK_STATE")
      .then(response => {
        if (response.code == "200") {
          that.PROCESS_CHECK_STATE = response.data.PROCESS_CHECK_STATE;
        }
      })
      .catch(error => {});
  },
  watch: {
    modal1(a) {
      if (a == false) {
        this.$refs["formValidateModel1"].resetFields();
      
        this.taskPeipleModel = false;
        this.fileId = !this.fileId;
        this.taskPeipleModel = false;
      }
    },
    "$store.state.firstLoginFlag": {
      handler(newVal) {
        this.firstLoginFlag = newVal;
      }
    }
  },
  data() {
    const validaterNextUser = (rule, value, callback) => {
      if (this.formValidateModel1.nextUser == "" && this.taskPeipleModel) {
        return callback(new Error("请选择一个人"));
      } else {
        callback();
      }
    };
    const validatertaskPeipleModelData2NextUser = (rule, value, callback) => {
      if (this.taskPeipleModelData2.nextUser == "" && this.taskPeipleModel2) {
        return callback(new Error("请选择一个人"));
      } else {
        callback();
      }
    };
    // const validaterNextStepName = (rule, value, callback) => {
    //   if (this.formValidateModel1.nextStepName == "" && this.taskPeipleModel) {
    //     return callback(new Error("请输入名称"));
    //   } else {
    //     callback();
    //   }
    // };
    return {
      buttonDisabled:false,
      firstLoginFlag: this.$store.state.firstLoginFlag,
      showModal: false,
      delateId: "",
      taskPeipleModel2: false,
      taskPeipleModelData2: {
        nextUser: ""
        // nextTitel: ""
      },
      ruleValidate2: {
        // nextTitel: [
        //   {
        //     type: "string",
        //     max: 1000,
        //     message: "您超出了范围",
        //     trigger: "blur"
        //   },
        //   {
        //     required: true,
        //     message: "此项不能为空",
        //     trigger: "blur"
        //   }
        // ],
        nextUser: [
          {
            required: false,
            validator: validatertaskPeipleModelData2NextUser,
            trigger: "on-check-change"
          }
        ]
      },
      tableHeaderAppend: [
        { name: "序" },
        { name: "追加内容" },
        { name: "追加附件" },
        { name: "所属部门" },
        { name: "操作时间" },
        { name: "操作人员" }
      ],
      tableBodyAppend: [
        {
          content: "", //内容
          dept: "", //所属部门
          createTime: "", //创建时间
          creator: "" //创建人
        }
      ],
      watchAppenModelAppendFileModel: false,
      watchAppenModel: false,
      watchTableCell: {
        bpmTitle: "",
        val1: "",
        val2: "",
        val3: "",
        val4: "",
        val5: "",
        val6: "",
        val7: "",
        val8: "",
        val9: "",
        val10: "",
        val11: ""
      },
      bpmName: "",
      appendId: "",
      appendbusId: "",
      fileId6: true,
      dataObj6: {},
      fileId5: true,
      dataObj5: {},
      fileId4: true,
      dataObj4: {},
      watchTableCellDitalModel: false,
      fileId3: true,
      dataObj3: {},
      watchFileModel: false,
      fileId2: true,
      dataObj2: {},
      buttonVal: "",
      modifyid: "",
      fileId: true,
      dataObj: {},
      uploadList6: [],
      uploadList5: [],
      uploadList4: [],
      uploadList3: [],
      uploadList2: [],
      // ===============
      uploadList: [],
      defaultList: [],
      data2: [],
      taskPeipleModel: false,
      PROCESS_CHECK_STATE: [],
      data1: [],
      ifExacte: "0", //查询方式
      minPubTime: "", //时间1
      maxPubTime: "", //时间2
      pageSize: 10,
      total: 0,
      pageNum: 1,
      addFlag: false,
      modifyFlag: false,
      modifyFlag: false,
      appendFlag: false,
      addFlagShow: false,
      modifyFlagShow: false,
      // =====================弹框一
      modal1: false,
      modalDisableFlag: true, //修改与新增弹框内容禁用标识
      formValidateModel1: {
        bpmTitle: "",
        val1: "",
        val2: true,
        val3: "",
        val4: "",
        val5: "",
        val6: "",
        val7: "",
        val8: "",
        val9: "",
        val10: "",
        val11: "",
        nextUser: "",
        appendAttachId: ""
      },
      tableHeader: [
        { name: "序" },
        { name: "操作" },
        { name: "标题" },
        { name: "状态" },
        { name: "是否公开" },
        { name: "管理部门" },
        { name: "追加内容" },
        { name: "附件" },
        { name: "发布人" },
        { name: "发布时间" },
        { name: "更新人" },
        { name: "更新时间" }
      ],
      tableBody: [],
      formValidate: {
        value2: "",
        val1: "",
        val2: "",
        val3: "",
        val4: "",
        val5: "",
        val6: []
      },
      ruleValidate: {
       bpmTitle: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "blur"
          },
          { type: "string", max: 50, message: "最大输入长度不能超过50字", trigger: "blur" }
        ],
        val10: [
          {
            type: "string",
            max: 1000,
            message: "您超出了范围",
            trigger: "blur"
          }
        ],
        nextUser: [
          {
            required: false,
            validator: validaterNextUser,
            trigger: "on-check-change"
          }
        ],
        // nextStepName: [
        //   {
        //     required: true,
        //     validator: validaterNextStepName,
        //     trigger: "on-blur"
        //   },
        //   {
        //     type: "string",
        //     max: 1000,
        //     message: "您超出了范围",
        //     trigger: "blur"
        //   }
        // ],
        val1: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "blur"
          },
          { max: 125, message: "输入长度过长", trigger: "blur" }
        ],
        val2: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val3: [{ max: 1000, message: "输入长度过长", trigger: "blur" }],
        val11: [{ max: 1000, message: "输入长度过长", trigger: "blur" }],
        val4: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val5: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val6: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val7: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val8: [
          {
            required: true,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        val9: [
          {
            required: false,
            message: "此项不能为空",
            trigger: "change"
          }
        ],
        Inputvalue1: [
          {
            required: false,
            message: "",
            trigger: "blur"
          }
        ],
        Inputvalue2: [
          {
            required: false,
            message: "",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    exportFn() {
      let that = this;
      let data = {
        title: this.formValidate.val1,
        flag: this.formValidate.val3,
        ifExacte: this.ifExacte,
        minPubTime: this.minPubTime,
        maxPubTime: this.maxPubTime, //时间2 */
        pageNum: this.pageNum,
        pageSize: this.pageSize
      };
      data = JSON.stringify(data);
      this.axios
        .post(
          "/process/infInstitutionalRiskManagement/getInfInstitutionalRiskManagementExcelList",
          data,
          {
            headers: { "Content-Type": "application/json" },
            responseType: "blob"
          }
        )
        .then(response => {
          this.download(response, "导出文档.xls");
        })
        .catch(error => {});
    },
    showProcess(item) {
      this.showModal = true;
      this.dataObj = {
        businessId: item.bpmBusId,
        name: "过程管理"
      };
    },
    //禁用非叶子节点
    treeNLeafNodeDisable(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children) {
          this.$set(treeDatas[i], "expand", true);
          this.$set(treeDatas[i], "disabled", true);
          this.treeNLeafNodeDisable(treeDatas[i].children);
        }
      }
    },
    watchAppenModelAppendFile(item) {
      // -----------------
      this.dataObj6 = {
        data: item,
        showFlag: false,
        disabled: false
      };
      this.fileId6 = item.busId;
      this.watchAppenModelAppendFileModel = true;
      // ---------------
    },
    watchAppendFun(a, b, c) {
      let that = this;
