    //新增修改的
    handleSubmitLast(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          if (
            this.addFlagShow == false &&
            this.modifyFlagShow == false &&
            this.addFlag == false
          ) {
            this.buttonDisabled = true;
            let that = this;
            let data = {
              bpmTitle: this.formValidateModel1.bpmTitle,
              bpmName: this.bpmName,
              title: this.formValidateModel1.val1,
              ifShow: this.formValidateModel1.val2,
              content: this.formValidateModel1.val3,
              nextUser: this.formValidateModel1.nextUser,
              attachId: this.formValidateModel1.attachId
            };
            data = JSON.stringify(data);
            this.axios
              .post(
                "/process/infInstitutionalRiskManagement/addInfInstitutionalRiskManagement",
                data,
                {
                  headers: { "Content-Type": "application/json" }
                }
              )
              .then(response => {
                if (response.code == "200") {
                  this.$Message.info("保存成功");
                  // this.fileId = !this.fileId;
                  // this.$refs[name].resetFields();
                  this.buttonDisabled = false;
                  this.modal1 = false;
                  this.taskPeipleModel = false;
                  this.tableList();
                }
              })
              .catch(error => {
                this.buttonDisabled = false;
              });
          } else if (
            this.addFlagShow == false &&
            this.modifyFlagShow == true &&
            this.addFlag == false
          ) {
            // ===============
            this.buttonDisabled = true;
            let that = this;
            let data = {
              bpmTitle: this.formValidateModel1.bpmTitle,
              bpmName: this.bpmName,
              title: this.formValidateModel1.val1,
              ifShow: this.formValidateModel1.val2,
              content: this.formValidateModel1.val3,
              nextUser: this.formValidateModel1.nextUser,
              attachId: this.formValidateModel1.attachId,
              id: this.modifyid
            };
            data = JSON.stringify(data);
            this.axios
              .post(
                "/process/infInstitutionalRiskManagement/updateInfInstitutionalRiskManagement",
                data,
                {
                  headers: { "Content-Type": "application/json" }
                }
              )
              .then(response => {
                if (response.code == "200") {
                  this.$Message.info("保存成功");
                  // this.$refs[name].resetFields();
                  this.buttonDisabled = false;
                  this.modal1 = false;
                  this.taskPeipleModel = false;
                  this.tableList();
                }
              })
              .catch(error => {
                 this.buttonDisabled = false;
              });
          }
        }
      });
    },
    datechange2(a) {
      this.formValidateModel1.val2 = a;
    },
    datechange3(a) {
      this.formValidateModel1.val3 = a;
    },
    pageSizeChange(a) {
      this.pageSize = a * 1;
      this.tableList();
    },
    pageChangeFun(a) {
      this.pageNum = a * 1;
      this.tableList();
    },
    selectTreeCleanUp(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children && treeDatas[i].children.length > 0) {
          this.selectTreeCleanUp(treeDatas[i].children);
        } else this.$delete(treeDatas[i], "children");
      }
    },
    datechange(a) {
      this.minPubTime = a[0];
      this.maxPubTime = a[1];
    },
    tableList() {
      let that = this;
      let data = {
        title: this.formValidate.val1,
        flag: this.formValidate.val3,
        ifExacte: this.ifExacte,
        minPubTime: this.minPubTime,
        maxPubTime: this.maxPubTime, //时间2 */
        pageNum: this.pageNum,
        pageSize: this.pageSize
      };
      data = JSON.stringify(data);
      this.axios
        .post(
          "/process/infInstitutionalRiskManagement/getInfInstitutionalRiskManagementList",
          data,
          {
            headers: { "Content-Type": "application/json" }
          }
        )
        .then(response => {
          if (response.code == "200") {
            that.tableBody = response.data.records;
            that.total = response.data.total;
          }
        })
        .catch(error => {});
    },
    delItem(a, b) {
      if (b == "1" || b == "4" || b == "7") {
        this.$Message.info("此状态不能操作");
      } else {
        if (b == "6" || b == "3") {
          this.$Message.info("此状态不能操作");
        } else {
          this.$Modal.confirm({
            title: "是否删除？",
            okText: "确认",
            cancelText: "取消",
            onOk: () => {
              // this.axios
              //   .get("/bpm/getNodeDefDetail?bpmType=process")
              //   .then(response => {
              //     this.bpmName = response.data[1].bpmName;
              //     let data = {
              //       bpmName: response.data[1].bpmName,
              //       nodeId: response.data[1].nodeId,
              //       roleName: response.data[1].roleName,
              //       bpmType: "process"
              //     };
              //     data = JSON.stringify(data);
              //     this.axios
              //       .post("/bpm/getNextRole", data, {
              //         headers: { "Content-Type": "application/json" }
              //       })
              //       .then(res => {
              //         if (res.code == "200") {
              //           this.delateId = a;
              //           this.taskPeipleModel2 = true;
              //           this.data2 = res.data;
              //           this.treeNLeafNodeDisable(this.data2);
              //           this.taskPeipleModel2 = true;

              //         }
              //       })
              //       .catch(error => {});
              //   })
              //   .catch(function(error) {});
              this.handleSubmitLast2(a);
            },
            onCancel: () => {}
          });
        }
      }
    },
    addfun(val, val2, val3, val4) {
      this.appendbusId = val2;
      this.appendId = "";
      if (val == "addFlag") {
        this.buttonVal = "下一步";
        this.formValidateModel1.val2 = true;
        this.formValidateModel1.val3 ="";
        this.modal1 = true;
        this.fileId = !this.fileId;
        this.addFlagShow = false;
        this.modifyFlagShow = false;
        this.addFlag = false;
        this.dataObj = {
          data: [],
          showFlag: true,
          disabled: false
        };
      } else if (val == "modifyFlag") {
        if (val4 == "1" || val4 == "4" || val4 == "7") {
          this.$Message.info("此状态不允许操作");
        } else {
          this.buttonVal = "下一步";
          this.modal1 = true;
          this.addFlagShow = false;
          this.modifyFlagShow = true;
          this.addFlag = false;
          this.modifyid = val3;
          this.axios
            .post(
              `/process/infInstitutionalRiskManagement/getEntityRow/${val3}`
            )
            .then(response => {
              if (response.code == "200") {
                this.formValidateModel1.attachId = response.data.attachId;
                this.formValidateModel1.val1 = response.data.title;
                this.formValidateModel1.val2 = JSON.parse(response.data.ifShow);
                this.formValidateModel1.val3 = response.data.content;
                this.formValidateModel1.bpmTitle = response.data.bpmTitle;
                this.dataObj = {
                  data: response.data,
                  showFlag: true,
                  disabled: false,
                  worfFlow: true
                };
                this.fileId = response.data.busId;
              }
            })
            .catch(function(error) {});
        }
      } else if (val == "appendFlag") {
        this.buttonVal = "保存";
        this.modal1 = true;
        this.addFlagShow = true;
        this.addFlag = true;
        this.modifyFlagShow = true;
        this.axios
          .post(`/process/infInstitutionalRiskManagement/getEntityRow/${val3}`)
          .then(response => {
            if (response.code == "200") {
              this.formValidateModel1.attachId = response.data.attachId;
              this.formValidateModel1.bpmTitle = response.data.bpmTitle;
              this.formValidateModel1.val1 = response.data.title;
              this.formValidateModel1.val2 = JSON.parse(response.data.ifShow);
              this.formValidateModel1.val3 = response.data.content;
              this.dataObj = {
                data: response.data,
                showFlag: true,
                disabled: true,
                worfFlow: true
              };
              this.fileId = response.data.busId;
            }
          })
          .catch(function(error) {});
        // -----------------
        let data2 = {
          relationBusId: val2
        };
        data2 = JSON.stringify(data2);
        this.axios
          .post(`/process/infProcessAppend/getInfProcessAppendRows`, data2, {
            headers: { "Content-Type": "application/json" }
          })
          .then(response => {
            if (response.code == "200") {
              this.appendId = response.data.id;
              this.formValidateModel1.val11 = response.data.content;
              this.dataObj2 = {
                data: response.data,
                showFlag: true,
                disabled: false,
                worfFlow: true
              };
              this.fileId2 = response.data.busId;
            }
          })
          .catch(function(error) {});
        // ---------------
      }
    },
    updateNextUser(nodes, node) {
      nodes.forEach(el => {
        if (el.nodeKey != node.nodeKey) {
          el.checked = false;
        }
      });
      this.formValidateModel1.nextUser = "";
      nodes.forEach(element => {
        if (element.checked == true)
          this.formValidateModel1.nextUser = element.code;
      });
      this.$refs["formValidateModel1"].validateField("nextUser");
    },
    updateNextUser2(nodes, node) {
      nodes.forEach(el => {
        if (el.nodeKey != node.nodeKey) {
          el.checked = false;
        }
      });
      this.taskPeipleModelData2.nextUser = "";
      nodes.forEach(element => {
        if (element.checked == true)
          this.taskPeipleModelData2.nextUser = element.code;
      });
      this.$refs["taskPeipleModelData2"].validateField("nextUser");
    },
    save1(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          if (
            this.addFlagShow == true &&
            this.addFlag == true &&
            this.modifyFlagShow == true
          ) {
            let data3 = {
              id: this.appendId,
              relationBusId: this.appendbusId,
              content: this.formValidateModel1.val11,
              attachId: this.formValidateModel1.appendAttachId,
              busId: this.busId
            };
            this.axios
              .post(`/process/infProcessAppend/updateInfProcessAppend`, data3, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                  this.$Message.info("保存成功");
                  this.modal1 = false;
                }
              })
              .catch(function(error) {});
            // ---------------
          } else {
            this.axios
              .get("/bpm/getNodeDefDetail?bpmType=process")
              .then(response => {
                this.bpmName = response.data[1].bpmName;
                let data = {
                  bpmName: response.data[1].bpmName,
                  nodeId: response.data[1].nodeId,
                  roleName: response.data[1].roleName,
                  bpmType: "process"
                };
                data = JSON.stringify(data);
                this.axios
                  .post("/bpm/getNextRole", data, {
                    headers: { "Content-Type": "application/json" }
                  })
                  .then(res => {
                    if (res.code == "200") {
                      this.data2 = res.data;
                      this.treeNLeafNodeDisable(this.data2);
                      this.taskPeipleModel = true;
                    }
                  })
                  .catch(error => {});
              })
              .catch(function(error) {});
            this.taskPeipleModel = true;
          }
        }
      });
    },
    rolefun() {},
    handleSubmit(name, a) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.ifExacte = a;
          this.pageNum = 1;
          this.tableList();
        } else {
        }
      });
    }
  }
};
</script>
